
h1 {
  margin: 0;
  font-size: 3rem;
  font-weight: bold;
}
.headline {
  font-family: Courier,Courier New,monospace;
  font-size: 15px;
  font-weight: 400;
  padding: 20px 0;
}
.main {
  max-width: 650px;
  margin: 5vh auto 20px;
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  font-size: 18px;
}
.code-wrap {
  text-align: left;
  background-color: #f8f8f8;
  padding: 2em 1.4em;
  line-height: 1.5em;
  margin: 0;
  overflow: auto;
  font-size: 1rem;
}
.code {
  margin: 0;
  padding: 0;
}
footer {
  margin-top: 40px;
  line-height: 2;
  font-size: 0.8rem;
  color: #c1c1c1;
  font-family: Courier,Courier New,monospace;
}
a {
  color: #39fda4;
}
.playground {
  margin-top: 20px;
}
.setting-label {
  text-align: left;
  font-size: 1rem;
  font-weight: 700;
  padding-top: 1rem;
}
.settings-container {
  display: -ms-flexbox;
  display: flex;
}
@media only screen and (max-width: 480px) {
.settings-container {
    display:block
}
}
.tab-content {
  background: #f7f7f7;
}
.settings-column {
  -ms-flex: auto;
      flex: auto;
  margin: 2rem 2rem 0;
  padding-bottom: 2rem;
  width: 80%;
}
.vue-swatches {
  display: -ms-flexbox;
  display: flex;
}
.vue-swatches__wrapper {
  padding-left: 0 !important;
}
.vue-swatches__container {
  padding: 5px 0 0 !important;
  margin-bottom: 0 !important;
}
.vue-tabs .nav>li span.title {
  font-weight: 700;
}
.inline-radio {
  display: -ms-flexbox;
  display: flex;
  border-radius: 3px;
  overflow: hidden;
  border: 0;
  margin-top: 10px;
}
.inline-radio div {
  position: relative;
  -ms-flex: 1;
      flex: 1;
  padding: 8px;
}
.inline-radio input {
  width: 100%;
  opacity: 0;
}
.inline-radio label {
  position: absolute;
  top: 0; left: 0;
  color: #333;
  width: 100%;
  height: 100%;
  background: #fff;
  font-weight: 500;
  font-size: 1rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  pointer-events: none;
  border-bottom: 3px solid #8e8e8e;
}
.inline-radio div:last-child label {
  border-right: 0;
}
.inline-radio input:checked + label {
  background: #d4d4d4;
}




